import React, { useEffect } from 'react';
import { useIntercom } from 'react-use-intercom';
import { useUser } from '@mssgme/hooks';

function IntercomWidget() {
    const { boot, shutdown } = useIntercom();
    const user = useUser();

    useEffect(() => {
        boot({
            email: user.email,
            userId: user._id,
        });
        return shutdown;
    }, []);

    return null;
}

export default IntercomWidget;
