import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

import IconCheckboxPassive from '../../../../icons/checkbox_passive.svg';
import IconCheckboxActive from '../../../../icons/checkbox_active.svg';
import styles from './styles.scss';

const Checkbox = ({ className, hasError, placeholder, styled, disabled, builderInput, ...rest }) => (
    <label
        className={classNames(
            className,
            styles.root,
            styles[styled],
            hasError && styles.error,
            disabled && styles.disabled,
            builderInput && styles.builderInput
        )}
    >
        <div className={styles.wrap}>
            <input className={styles.input} type="checkbox" disabled={disabled} {...rest} />

            {styled === 'toggle' ? (
                <div className={styles.toggler} />
            ) : (
                <>
                    <IconCheckboxPassive className={styles.unchecked} />
                    <IconCheckboxActive className={styles.checked} />
                </>
            )}
        </div>
        {placeholder && <div className={styles.label}>{placeholder}</div>}
    </label>
);

Checkbox.propTypes = {
    className: PropTypes.string,
    placeholder: PropTypes.any,
    hasError: PropTypes.bool,
    disabled: PropTypes.bool,
    styled: PropTypes.oneOf(['default', 'toggle']),
    builderInput: PropTypes.bool,
};

Checkbox.defaultProps = {
    checked: false,
    hasError: false,
    disabled: false,
    styled: 'default',
    builderInput: false,
};

export default Checkbox;
