import React from 'react';
import { render } from 'react-dom';
import { Workbox } from 'workbox-window';

import { configureStore } from '@mssgme/store';
import { UserAdminApp } from '@mssgme/webapp-core';

if (!(__DEV__ && !__DEV__.sw)) { //TODO: redo
    if ('serviceWorker' in navigator) {
        const wb = new Workbox('/sw.js');
        wb.register();
    }
}

const store = configureStore();

render(<UserAdminApp store={store} />, document.getElementById('root'));
