import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback, useMemo, useState } from 'react';
import { FiCalendar, FiMail, FiPhoneCall } from 'react-icons/fi';
import { useTranslation } from 'react-i18next';

import { formatFormData } from '@mssgme/shared';
import { LinkBlank, CopyContainer } from '@mssgme/ui';
import { CopyIcon, MapPinIcon } from '@mssgme/icons';

import styles from './FeedbackItemForm.scss';

const fdIcons = {
    date: FiCalendar,
    datetime: FiCalendar,
    address: MapPinIcon,
    phone: FiPhoneCall,
    email: FiMail,
};

export const DetailView = ({ item, cut = false }) => {
    const { t } = useTranslation('landings');
    const { link, normalized, Icon } = useMemo(() => ({ ...formatFormData(item), Icon: fdIcons[item.type] }), [
        item.type,
        item.value,
    ]);
    const [renderValue, setRenderValue] = useState(
        cut && normalized.length > 100 ? normalized.slice(0, 100) + '...' : normalized
    );
    const handleUncut = useCallback(() => setRenderValue(normalized), [normalized]);
    const isCut = renderValue.length < normalized.length;
    const Tag = link ? LinkBlank : 'div';

    return (
        <Tag className={styles.dataView} {...link && { href: link }}>
            <CopyContainer
                className={styles.copyContainer}
                tagClassName={styles.copyTag}
                buttonClassName={styles.copyButton}
                text={normalized}
                copyTitle={<CopyIcon />}
            >
                <div className={cn(styles.dataContainer, isCut && cut && styles.cut)} onClick={handleUncut}>
                    {renderValue || (
                        <span style={{ color: 'rgba(0,0,0,0.35)' }}>{t('Feedback.Edit.Item.placeholders.empty')}</span>
                    )}
                    {isCut && (
                        <span style={{ color: '#007aff', marginLeft: 3, fontSize: 14 }}>
                            {t('Feedback.Edit.Item.labels.more')}
                        </span>
                    )}
                </div>
                {Icon && <Icon size={20} />}
            </CopyContainer>
        </Tag>
    );
};

DetailView.propTypes = {
    item: PropTypes.shape({
        type: PropTypes.oneOf(['name', 'phone', 'email', 'text', 'date', 'datetime', 'address']).isRequired,
        label: PropTypes.string,
        value: PropTypes.string,
    }).isRequired,
    cut: PropTypes.bool,
};
