import cn from 'classnames';
import React from 'react';
import PropTypes from 'prop-types';

import { __t } from '@mssgme/shared';
import Cover from '../Cover/Cover';
import styles from './HatCompact.scss';

export default function HatCompact({ paneColor, logoSrc, textColor, title, subtitle, align, ...rest }) {
    const style = rest.hasPadding
        ? {
            borderTopLeftRadius: 16,
            borderTopRightRadius: 16,
            borderBottomLeftRadius: 16,
            borderBottomRightRadius: 16,
        }
        : undefined;

    return (
        <div className={cn(styles.compact, align === 'center' && styles.center)}>
            {rest.coverSrc && <Cover style={style} {...rest} />}

            <div className={cn(styles.paneWrapper, rest.coverSrc ? styles.withCover : styles.withPadding)}>
                {align === 'center' ? (
                    <div className={styles.pane} style={{ backgroundColor: paneColor }}>
                        <div className={styles.logoBox} style={{ backgroundColor: logoSrc ? 'transparent' : '#ccc' }}>
                            {logoSrc && <img src={logoSrc} alt="Logo" />}
                        </div>
                        <div className={styles.contentWrapper}>
                            <div className={styles.content} style={{ color: textColor }}>
                                <h1>{title}</h1>
                                <p style={{ opacity: 0.8 }}>{subtitle}</p>
                            </div>
                        </div>
                    </div>
                ) : (
                    <div
                        className={styles.pane}
                        style={{
                            backgroundColor: paneColor,
                            flexDirection: align === 'right' ? 'row-reverse' : 'row',
                        }}
                    >
                        <div className={styles.logoBox} style={{ backgroundColor: logoSrc ? 'transparent' : '#ccc' }}>
                            {logoSrc && <img src={logoSrc} alt="Logo" />}
                        </div>
                        <div className={styles.contentWrapper}>
                            <div className={styles.content} style={{ color: textColor }}>
                                <h1>{title}</h1>
                                <p style={{ opacity: 0.8 }}>{subtitle}</p>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
}

HatCompact.propTypes = {
    blockStyles: PropTypes.object.isRequired,
    hasPadding: PropTypes.bool,
    coverSrc: PropTypes.string,
    logoSrc: PropTypes.string,
    paneColor: PropTypes.string,
    textColor: PropTypes.string,
    title: PropTypes.string,
    align: PropTypes.oneOf(['left', 'right', 'center']),
    subtitle: PropTypes.string,
    isMobileMode: PropTypes.bool,
};

HatCompact.defaultProps = {
    hasPadding: true,
    coverSrc: '',
    logoSrc: '',
    align: 'left',
    paneColor: '#ffffffff',
    textColor: '#333333ff',
    title: __t('header'),
    subtitle: __t('subheader'),
};
