import cn from 'classnames';
import PropTypes from 'prop-types';
import React from 'react';
import { FiMail, FiPhone, FiType, FiUser } from 'react-icons/fi';

import styles from './SubscriptionFormItemsField.scss';

export const SUBSCRIPTION_FORM_ITEM_TYPES = {
    name: { icon: FiUser },
    phone: { icon: FiPhone },
    email: { icon: FiMail },
    text: { icon: FiType, props: { large: true } },
};

export const SubscriptionFormItem = ({ value, className, textClassName }) => {
    const Icon = SUBSCRIPTION_FORM_ITEM_TYPES[value.type]?.icon || FiType;

    return (
        <div className={className}>
            <div className={cn(textClassName, styles.label)}>
                <div className={styles.icon}>
                    <Icon size={20} />
                </div>
                <span>{value.label.replace(/\n/g, ' ')}</span>
                <span>{value.required && '*'}</span>
            </div>
        </div>
    );
};

SubscriptionFormItem.propTypes = {
    value: PropTypes.object.isRequired,
    onEdit: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
    className: PropTypes.string,
    textClassName: PropTypes.string,
};
