import React, { useEffect } from 'react';
import { Redirect } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { useActions, useUser, useFetch, useAutoSwitchApp, useWorkspace } from '@mssgme/hooks';
import { NotificationCenter } from '@mssgme/ui';
import { SplashScreen, AccountBlocked } from '../../UI';
import { MainPage } from '../MainPage';
import { EmptyWorkspace } from '../EmptyWorkspace';

export function PrivatePage(props) {
    const { t } = useTranslation();
    const { api } = useActions();
    const init = useFetch(api.init);
    const user = useUser();
    const workspace = useWorkspace();
    useAutoSwitchApp();

    useEffect(() => {
        if (!init.rejected) {
            return;
        }

        const center = NotificationCenter.getInstance();
        const id = center.danger(t('common:errorsList.unauthorized'), { showClose: true });

        return () => {
            center.remove(id);
        };
    }, [init.rejected]);

    if (init.rejected) {
        // TODO: show error
        window.location.href = '/auth/logout';

        return null;
    }

    if (!init.requestCount || init.pending || !user) return <SplashScreen />;

    if (user?.blocked || workspace?.blocked) {
        return <AccountBlocked isBlockedByIp={user?.isBlockedByIp} />;
    }

    // if (!user.email) {
    //     return (
    //         <Switch>
    //             <Route exact path="/enter-email" component={EnterEmailPage} />
    //             <Redirect to="/enter-email" />
    //         </Switch>
    //     );
    // }

    if (user.workspaces.length === 0) {
        return <EmptyWorkspace />;
    }

    if (!workspace) {
        return <SplashScreen />;
    }

    return <MainPage {...props} />;
}
