import PropTypes from 'prop-types';
import React, { Suspense } from 'react';
import { Route, Router, Switch } from 'react-router-dom';
import { Provider } from 'react-redux';
import { IntercomProvider } from 'react-use-intercom';
import { useTranslation } from 'react-i18next';

import { history } from '@mssgme/services';
import { useI18n } from '@mssgme/i18n';
import {
    EventEmitterProvider,
    ExperimentsProvider,
    ModalNotificationProvider,
    NotificationsBag,
    renderNode,
    useUITranslations,
} from '@mssgme/ui';
import { useLandingsTranslations } from '@mssgme/landings';
import { useBillingTranslations } from '@mssgme/billing';
// import { useInboxTranslations } from '@mssgme/inbox';
import { useProductsTranslations } from '@mssgme/products';
// import { useBotsTranslations } from '@mssgme/chatbot';

import { SplashScreen, InstallPromptProvider } from '../UI';
import { PrivatePage, PrivateRoute, AuthorizePage } from '../Pages';
import { HelpWidget } from './HelpWidget';

import config from 'config';

const ab = {
    experiments: [
        // {
        //     id: 'collections_field',
        //     variants: [{ id: 'eye', weight: 90 }, { id: 'plus', weight: 50 }],
        // },
    ],
};

const LocalesLoader = ({ children }) => {
    const { i18n } = useI18n('common', (language) =>
        import(
            /* webpackChunkName: "locales.common" */
            /* webpackInclude: /\.json$/ */
            `../../locales/${language}/common.json`
        )
    );
    useUITranslations();
    useLandingsTranslations();
    useBillingTranslations();
    // useInboxTranslations();
    useProductsTranslations();
    // useBotsTranslations();

    return children ? renderNode(children, i18n) : null;
};

const AdminCore = () => {
    const { i18n } = useTranslation();

    return (
        <EventEmitterProvider>
            <ModalNotificationProvider />
            <NotificationsBag />
            <Switch>
                <Route path="/authorize" exact component={AuthorizePage} />
                <PrivateRoute path="/" component={PrivatePage} />
            </Switch>

            {!__DEV__ && <HelpWidget lang={i18n.language} />}
        </EventEmitterProvider>
    );
};

export const UserAdminApp = ({ store }) => {
    const content = (
        <ExperimentsProvider config={ab}>
            <LocalesLoader />
            <InstallPromptProvider>
                <Suspense fallback={<SplashScreen />}>
                    <Provider store={store}>
                        <Router history={history}>
                            <AdminCore />
                        </Router>
                    </Provider>
                </Suspense>
            </InstallPromptProvider>
        </ExperimentsProvider>
    );

    if (config.intercom_app_id) {
        return <IntercomProvider appId={config.intercom_app_id}>{content}</IntercomProvider>;
    }

    return content;
};

UserAdminApp.propTypes = {
    store: PropTypes.object.isRequired,
};
