import { useMemo } from 'react';
import { useRouteMatch } from 'react-router-dom';

import { useFeature } from '@mssgme/hooks';

export const useBuilderModes = () => {
    const showDimensionsSwitch = useFeature('desktop_layout');
    const isViewMode = !!useRouteMatch('/pages/-/:any/edit/preview');
    const isMobileMode = (!useRouteMatch('/pages/-/:any/edit/preview/desktop') && isViewMode) || !showDimensionsSwitch;
    const isInFormatSelectionMode = showDimensionsSwitch && isViewMode;
    const isDesktopPreviewMode = isInFormatSelectionMode && !isMobileMode;

    return useMemo(
        () => ({
            isViewMode,
            isMobileMode,
            isDesktopPreviewMode,
            isInFormatSelectionMode,
        }),
        [isViewMode, isMobileMode, isDesktopPreviewMode, isInFormatSelectionMode]
    );
};
