import { useEffect } from 'react';

export const useBodyStyle = (style) => {
    useEffect(() => {
        if (!style) {
            return;
        }

        const prev = {};

        for (const key in style) {
            prev[key] = document.body.style[key];
        }

        Object.assign(document.body.style, style);

        return () => {
            Object.assign(document.body.style, prev);
        };
    }, [!!style]);
};
